.contactForm,
.inputWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 410px;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 14px;
  padding: 20px;
  box-shadow: 0px 4px 6px 1px rgba(0, 0, 0, 0.3);
  margin-bottom: 30px;
}

.inputLabel {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

input {
  width: 400px;
  height: 26px;
  border: 1px solid grey;
  border-radius: 6px;
  padding: 6px;
  font-size: 16px;
}

[type="submit"] {
  width: 100%;
  height: 36px;
  font-size: 16px;
  font-weight: 600;
  background-color: #6c6cff;
  border: none;
  border-radius: 6px;
  color: #ffffff;
  padding: 4px;
  margin-top: 10px;
}
[type="submit"]:hover {
  cursor: pointer;
  background-color: #4949ff;
}
[type="submit"]:active {
  box-shadow: inset -1px -1px 23px 9px rgba(0, 0, 0, 0.17);
}

ul,
p {
  padding: 0;
  margin: 0;
}

li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 410px;
  font-family: Helvetica, Arial, sans-serif;
  border-radius: 4px;
  font-size: 16px;
  padding: 10px 20px 10px 20px;
  border: 1px solid rgb(175, 175, 175);
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.3);
  margin-bottom: 10px;
}

[type="button"] {
  width: 30px;
  height: 30px;
  margin-left: 10px;
  background-color: #ff846e;
  border: none;
  border-radius: 6px;
  padding: 0 6px 3px 6px;
  font-size: 20px;
  font-weight: 600;
  color: #ffffff;
}
[type="button"]:hover {
  background-color: #fc6348;
  cursor: pointer;
}
[type="button"]:active {
  box-shadow: inset -1px -1px 23px 9px rgba(0, 0, 0, 0.17);
}

h2 {
  font-family: Helvetica, Arial, sans-serif;
  color: #4949ff;
}

.error {
  position: absolute;
  top: 0;
  right: 10px;
  width: 300px;
  height: 80px;
  border-radius: 6px;
  background-color: crimson;
  font-family: Helvetica, Arial, sans-serif;
  color: white;
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
}
